.statistics {
    font-size: 13px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: white;
    box-shadow: 0px 0px 3px 1px rgba(185, 185, 185, 1);
    margin-left: 30px;
    width: 250px;
  }
  
  .statList {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    width: 100%;
  }
  
  .item {
    color: rgb(215, 221, 237);
    text-shadow: -0.5px 0 black, 0 0.5px black, 0.5px 0 black, 0 -0.5px black;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
  }
  
  .label {
    font-size: 10px;
    padding-bottom: 5px;
    font-weight: 600;
  }
  
  .title {
    text-transform: uppercase;
    font-weight: 600;
    color: rgb(72, 75, 77);
    font-size: 15px;
    padding: 15px 0;
  }